class Conversation {
    constructor(text) {
        this.text = text;
    }

    sintetizar(texto, opcion, p, div, img) {
        img.src = texto["author"].img
        div.appendChild(img);
        div.appendChild(p);

        const msg = new SpeechSynthesisUtterance();

        msg.lang = texto["author"]["lang"];
        msg.text = texto["text"];
        msg.rate = texto["author"].parametros["rate"];
        msg.pitch = texto["author"].parametros["pitch"];


        if (opcion == 's') {
            msg.onstart = () => { this.frase(texto) };
        } else if (opcion == 'w') {
            let windex = 0;

            msg.onboundary = (event) => {

                this.wordToWord(texto, windex, p, div, img);
                windex++;

            };

        }

        speechSynthesis.speak(msg);

    }
    frase(texto) {
        const caja = document.querySelector("#box");
        const div = document.createElement('div');
        div.className = "chat"
        const p = document.createElement('p');
        const img = document.createElement('img');
        p.textContent = texto["author"].nombre + ": " + texto["text"];
        p.style = "color:" + texto["author"].parametros["color"]
        img.src = texto["author"].img

        caja.appendChild(div);
        div.appendChild(img)
        div.appendChild(p)

    }
    wordToWord(texto, indice, p, div, img) {
        const words = texto.text.split(" ");
        console.log(words[indice])
        p.textContent = p.textContent + " " + words[indice];
        p.style = "color:" + texto["author"].parametros["color"];

        div.childNodes[1].data = p;


    }
    letterToLetter(texto) {

    }

    iniciar(opcion) {

        for (let i of this.text) {
            const div = document.createElement('div');
            const caja = document.querySelector("#box");
            div.className = "chat"
            const p = document.createElement('p');
            const img = document.createElement('img');

            caja.appendChild(div);


            this.sintetizar(i, opcion, p, div, img)

        }
    }


}
export default Conversation;